import React from "react";
import scssObj from "../../App.scss";
import {Link} from 'react-router-dom';
import PropTypes from "prop-types";
import history from '../../history.js';

// import { connect } from 'react-redux';
// import { status } from '../../Redux/actions/action.js';

class Home extends React.Component {
  constructor(props,context){
    super(props,context)
    // this.state = {
    //   actived: 1
    // }  
    this.linkPerson = this.linkPerson.bind(this);
  }
  // static contextTypes = {
  //   router: PropTypes.object
  // }
  // componentWillReceiveProps(nextStatus){
  //   if(nextStatus){
  //     this.setState({
  //       actived:2
  //     })
  //   }
  // }
  componentDidMount(){
    if(localStorage.remember == "TRUE" || sessionStorage.logined == "Y"){
      document.getElementById("loginbutton").style.display = "none";
      document.getElementById("personspace").className = "dropdown show";
      document.getElementById("exit").style.display = "block";
    }
  }
  exitlogin(){
    sessionStorage.logined = "N";
    localStorage.remember = "";
    document.getElementById("exit").style.display = "none";
    document.getElementById("personspace").style.display = "none";
    window.location.href = "/";
  }
  linkPerson(){
    // this.props.history.push('/person');
    history.push("/person");

  }
  render() {
    return (
      <div>
        <nav className={ scssObj.position + " navbar navbar-expand-lg navbar-dark " }>
          <div className={scssObj.wrap}>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarText"
              aria-controls="navbarText"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon" />
            </button>
            <div className="collapse navbar-collapse" id="navbarText" >
              <h1 className={ scssObj.brand }>bulb</h1>   
              <ul className= { scssObj.navUl + " navbar-nav mr-auto"}>
              {/* <Link to="/"><img src={ require('../../images/51miz-E370681-A04BFB812.png') } alt="" className={ scssObj.logo} /></Link>  */}
              <Link to="/"><img src={ require('../../images/51miz-E370681-A04BFB812.png') } alt="" className={ scssObj.logo} /></Link> 
                <li className="nav-item ">
                  <Link className="nav-link" to="/" >
                  
                     <span className="sr-only">(current)</span>
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/rank" >
                    
                  </Link>
                </li>
                {/* <li className="nav-item">
                  <Link className="nav-link" to="friends" >
                    
                  </Link>
                </li> */}
              </ul>
              <form className={scssObj.search + " form-inline"}>
                <input
                  className="form-control mr-sm-2"
                  type="search"
                  placeholder="Search"
                  aria-label="Search"
                />
                <button
                  className="btn btn-outline-light"
                  type="submit"
                >
                  Search
                </button>
              </form>
              <div className={scssObj.buttons} id="loginbutton">
               <Link to="/login"><button type="button" className="btn btn-secondary btn-sm"></button></Link>
               <Link to="/register"><button type="button" className="btn btn-secondary btn-sm"></button></Link>
              </div>
               {/* <Link to="/person" id="person" className={scssObj.person}></Link> */}

               {/* <div class={scssObj.dropdown + " dropdown show"} id="personspace">
                  <a class="btn btn-secondary dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    
                  </a>

                  <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
                    <a class="dropdown-item" href="#"></a>
                    <a class="dropdown-item" href="#"></a>
                    <a class="dropdown-item" href="#"></a>
                  </div>
                </div> */}

                  <div className={scssObj.dropdown + " btn-group"} id="personspace">
                 
                  <button type="button" className="btn btn-secondary" onClick={this.linkPerson}></button>
                  <button type="button" className="btn btn-secondary dropdown-toggle dropdown-toggle-split" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <span className="sr-only"></span>
                    </button>
                    <div className="dropdown-menu">
                      <Link className="dropdown-item" to="/person"></Link>
                      <Link className="dropdown-item" to="/message"></Link>
                      <Link className="dropdown-item" to="/set"></Link>
                      <Link className="dropdown-item" to="/help"></Link>                     
                      <a className="dropdown-item" id="exit" onClick={this.exitlogin}></a>                     
                    </div>
                  </div>

           

               {/* <a href="#" onClick={this.exitlogin} id="exit" className={scssObj.exit}></a> */}
            </div>
          </div>
        </nav>
      </div>
    );
  }
}

export default Home;



