import React, { Component } from 'react'
import scssObj from "../../../App.scss";
import {Link} from "react-router-dom"

class Group extends Component {
  constructor(props){
    super(props)
    this.state = {
   
    }
    
  }
  componentDidMount = () => {
   
  }
  render(){
    return(
        // !this.state.sign? 'loading' : (
        <div className={scssObj.classlistLayout} id="keji">
        <div className="container">
          <div className="row">
            <div className="col-3">
            <div className= {scssObj.classlist + " list-group"}>
              <Link to="/person" className="list-group-item list-group-item-action "></Link>
              <Link to="/friends" className="list-group-item list-group-item-action"></Link>
              <Link to="/group" className="list-group-item list-group-item-action active"></Link>
              <Link to="/takein" className="list-group-item list-group-item-action"></Link>
              <Link to="/follow" className="list-group-item list-group-item-action"></Link>
            </div> 
            </div>    
        
             <div className={scssObj.person + " col-9"}>
              <div className={scssObj.content}>
                <div><h3></h3></div>               
                <div className={scssObj.item}>
                <h2></h2>
                </div>
                
              </div>
             
             </div>
     
           
          </div>
        </div>
         
         
        </div>
    )
}
}

export default Group;


